<template>
  <v-card>
    <v-card-text>
      <mrc_ess_staffMsg v-model="list.staff_uuid"
                        :disabled='$store.state.uuid?true:false'
                        :iconShow='true'
                        :ErrMsg="$errors('list','staff_uuid',requiredRule)"
                        @changeList="changeList"></mrc_ess_staffMsg>
      <div style="background-color: #f5f5f5"
           class="mt-4"
           v-if="list.staff_uuid">
        <v-row class="pl-6 pt-6 pr-6">
          <v-col cols="6">
            <mrc_ess_prompt :label="this.$t('label_The_original_company')+'*'"
                            :rules='false'
                            :chips='false'
                            :disabled='true'
                            value_field='uuid'
                            view_fields='org_level_name'
                            :select_arr='$store.state.topcompanylist'
                            v-model="viewList.org_level_uuid">
            </mrc_ess_prompt>
          </v-col>
          <v-col cols="6">
            <mrc_ess_prompt :label="this.$t('label_The_original_department')+'*'"
                            :rules='false'
                            :disabled='true'
                            v-model="viewList.dept_uuid"
                            :obj="deptuuid">
            </mrc_ess_prompt>
          </v-col>
          <v-col cols="12">
            <mrc_ess_prompt :label="this.$t('label_The_original_position')+'*'"
                            :rules='false'
                            :disabled='true'
                            v-model="viewList.job_post_uuid"
                            :obj="job_post_uuid">
            </mrc_ess_prompt>
          </v-col>
          <v-col cols="12">
            <!-- <mrc_ess_staffMsg v-model="viewList.direct_leader_code"
                            :disabled='true'
                            :staffText='staffText'></mrc_ess_staffMsg> -->
            <mrc_ess_prompt :label="this.$t('label_Former_boss')"
                            :rules='false'
                            :disabled='true'
                            v-model="viewList.direct_leader_code"
                            :obj="superior">
            </mrc_ess_prompt>
          </v-col>
          <v-col cols="12">
            <mrc_ess_prompt :label="this.$t('label_The_original_role')"
                            :rules='false'
                            :multiple="true"
                            :disabled='true'
                            v-model="viewList.role_uuids"
                            :obj="spica">
            </mrc_ess_prompt>
          </v-col>
        </v-row>
      </div>
      <div>
        <v-row>
          <v-col cols="6">
            <mrc_ess_datePicker @getTime='paramsChange'
                                :errMsg="$errors('list','Change_effective',requiredRule)"
                                :disabled="disabled"
                                :label="this.$t('label_Effective_date')+'*'"
                                v-model="list.Change_effective"></mrc_ess_datePicker>
          </v-col>
          <v-col cols="6">
          </v-col>
        </v-row>

        <div class="pl-6 pt-6 pr-6"
             style="border:2px solid rgb(238, 238, 240)">
          <v-row>
            <v-col cols="6">
              <mrc_ess_prompt :label="this.$t('label_New_company')"
                              :rules='false'
                              value_field='uuid'
                              view_fields='org_level_name'
                              :select_arr='$store.state.topcompanylist'
                              :disabled='disabled'
                              v-model="list.transferred_org_level_uuid">
              </mrc_ess_prompt>
            </v-col>
            <v-col cols="6">
              <mrc_ess_prompt :label="this.$t('label_New_department')"
                              :rules='false'
                              :disabled='disabled'
                              v-model="list.transferred_dept_uuid"
                              :obj="newdeptuuid">
              </mrc_ess_prompt>
            </v-col>
            <v-col cols="6">
              <mrc_ess_prompt :label="this.$t('label_New_position')"
                              :rules='false'
                              :disabled='disabled'
                              v-model="list.transferred_post_uuid	"
                              :obj="newjob_post_uuid">
              </mrc_ess_prompt>
            </v-col>
            <v-col cols="6">
              <!--   :errMsg="$errors('list','job_post_grade_uuid',requiredRule)" -->
              <mrc_ess_prompt label="新岗位等级"
                              :rules='false'
                              :disabled='disabled'
                              :multiple="true"
                              v-model="list.transferred_post_grade"
                              :obj="newmrbase_sys_job_post_grades">
              </mrc_ess_prompt>
            </v-col>
            <v-col cols="12">
              <mrc_ess_prompt :label="this.$t('label_New_boss')"
                              :rules='false'
                              :disabled='disabled'
                              v-model="list.direct_leader_code"
                              :obj="newsuperior">
              </mrc_ess_prompt>
            </v-col>
            <v-col cols="12">
              <mrc_ess_prompt :label="this.$t('label_The_new_role')"
                              :rules='false'
                              :multiple="true"
                              :disabled='disabled'
                              v-model="list.transferred_role_uuid"
                              :obj="newspica">
              </mrc_ess_prompt>
            </v-col>
          </v-row>
        </div>
        <v-col cols="12">
          <!-- <v-text-field label="备注"
                      v-model="list.remark"
                      :readonly="disabled">
        </v-text-field> -->
          <mrc_ess_multiText :label="this.$t('label_note')"
                             :isNational="judegNational('remark')"
                             v-model="list.remark"
                             :readonly="disabled">
          </mrc_ess_multiText>
        </v-col>
      </div>
    </v-card-text>
    <mrc_ess_submitBtn @success='success'
                       @cancel='cancel'
                       :loading='btnIsLoading'
                       v-if='!disabled'></mrc_ess_submitBtn>
  </v-card>
</template>

<script> 
import {
  getStaffVariationList
  , detailStaffVariation
  , addStaffVariation
  , updateStaffVariation
  , deleteStaffVariation
} from '@/service/api/staffmsg.js'
import { required, maxLength, between, email, url } from 'vuelidate/lib/validators'
export default {
  components: {},
  props: {
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      list: {},
      btnIsLoading: false,
      viewList: {},
    };
  },
  computed: {
    // staffText: [this.$t('label_Former_boss') + '*', ''],
    deptuuid () {  //部门参照查询字段
      return { prompt_name: 'levelUuid_deptUuid', org_level_uuid: this.company_uuid }
    },
    company_uuid () { //当前选中公司
      return this.$store.state.currentCompany
    },
    company_Name () { //当前选中公司名字
      const items = this.$store.state.topcompanylist.filter(item => item.uuid == this.$store.state.currentCompany)
      return items[0].org_level_name
    },
    job_post_uuid () {  //岗位查询
      return { prompt_name: 'level-dept-post', dept_uuid: this.viewList.dept_uuid }
    },
    superior () { //上司高级参照
      return { prompt_name: 'boss', org_level_uuid: this.company_uuid, staff_uuid: this.list.staff_uuid }
    },
    spica () { //角色高级参照 
      return { prompt_name: 'level-role', org_level_uuid: this.company_uuid }
    },
    newdeptuuid () {  //部门参照查询字段
      return { prompt_name: 'levelUuid_deptUuid', org_level_uuid: this.list.transferred_org_level_uuid }
    },
    newjob_post_uuid () {  //岗位查询
      return { prompt_name: 'level-dept-post', dept_uuid: this.list.transferred_dept_uuid }
    },
    newsuperior () { //上司高级参照
      return { prompt_name: 'new_boss', org_level_uuid: this.list.transferred_org_level_uuid }
    },
    newspica () { //角色高级参照 
      return { prompt_name: 'level-role', org_level_uuid: this.list.transferred_org_level_uuid }
    },
    newmrbase_sys_job_post_grades () { //岗位等级
      return { prompt_name: 'mrbase_sys_job_post_grades', org_level_uuid: this.list.transferred_org_level_uuid }
    }
  },
  created () { },
  watch: {},
  async mounted () {
    await this.national('mrbase_sys_staffs_change_cont')
    if (this.$store.state.uuid) {
      this.getDetail()
      this.$route.meta.title = this.$store.state.subPage_name
    }
    else {

    }
  },

  methods: {
    paramsChange () { },
    changeList (val) {
      // console.log(val);
      if (val.role_uuids) {
        val.role_uuids = String(val.role_uuids).indexOf(',') != -1 ? String(val.role_uuids).split(',') : [val.role_uuids]
      }
      this.viewList = val
    },
    async getDetail () {
      const res = await detailStaffVariation({ uuid: this.$store.state.uuid })
      if (res.code == 200) {
        this.list = res.data
        // console.log(this.list);
        this.list.transferred_role_uuid = String(this.list.transferred_role_uuid).indexOf(',') != -1 ? this.list.transferred_role_uuid.split(',') : [this.list.transferred_role_uuid]
        this.list.transferred_post_grade = String(this.list.transferred_post_grade).indexOf(',') != -1 ? this.list.transferred_post_grade.split(',') : [this.list.transferred_post_grade]
        // console.log(this.list);
      }
      else {

      }
    },
    async success () {
      this.$v.list.$touch()
      if (this.$v.list.$error) return
      this.btnIsLoading = true
      delete this.list.result_status
      String(this.list.Change_effective).indexOf('-') == -1 ? this.list.Change_effective = this.$moment(this.list.Change_effective) : ''
      this.list.transferred_role_uuid ? this.list.transferred_role_uuid = this.list.transferred_role_uuid.join() : ''
      this.list.transferred_post_grade ? this.list.transferred_post_grade = this.list.transferred_post_grade.join() : ''
      // console.log(this.list);
      // return
      if (this.$store.state.uuid) {
        updateStaffVariation(this.$deletefield(null, this.list)).then(res => {
          if (res.code == 200) {
            this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
            this.getDetail()
          }
          else {
            this.$store.commit('newsTips', this.$t('message_The_modification_failed') + res.msg)
          }
          this.btnIsLoading = false
        })
      }
      else {
        addStaffVariation(this.list).then(res => {
          if (res.code == 200) {
            this.$store.commit('newsTips', this.$t('message_added_successfully'))
            this.$router.push({ name: 'staffVariation' })
          }
          else {
            this.$store.commit('newsTips', res.msg)
          }
          this.btnIsLoading = false
        })
      }
    },
    cancel () {
      this.$myConfirm({
        content: this.$t('message_Confirm_the_cancellation_of_the_edit')
      }).then(res => {
        this.$router.push({
          name: 'staffVariation'
        })
      }).catch(err => { })
    },
  },
  validations: {
    list: {
      staff_uuid: {
        required
      },
      Change_effective: {
        required
      },
    }
  },
};
</script>

<style scoped>
</style>

